body {
  padding: 0px;
  margin: 0px;
  height: 100%;
}

.horizontal-line {
  height: 0.5rem;
  width: 3rem;
}

.vertical-line {
  height: 3rem;
  width: 0.5rem;
}

.dot {
  background-color: orange !important;
  height: 0.5rem;
  width: 0.5rem;
  border-color: black;
  border-style: solid;
  border-width: 0.05rem;
}

.line {
  border-color: black;
  border-style: solid;
  border-width: 0.05rem;
  margin: 0px;
}

.unit {
  width: 3rem;
}

.unit-top {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.unit-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.game-board {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 21rem;
  margin: auto;
  padding: 1rem;
}

.main-page-body {
  background-color: beige;
  overflow: auto;
}

.main-page-info {
  border-color: black;
  border-style: solid;
  border-width: 0.2rem;
  width: 50%;
  margin: auto;
  margin-top: 2rem;
}

.main-page-title {
  margin: 0px;
  padding: 2rem;
  font-size: 5rem;
  text-align: center;
}

.main-page-desc {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 3rem;
}

.main-page-desc-items {
  margin: 1rem;
  padding: 1rem;
}

.main-page-button-div {
  justify-content: center;
  display: flex;
  margin: 2rem;
}

.main-page-button {
  font-size: 3rem;
  color: orange;
  padding: 1rem;
  cursor: pointer;
}

.main-page-footer {
  font-size: 2rem;
  text-align: center;
  width: 100%;
  font-weight: "bold";
}

.game-banner {
  background-color: orange;
  font-size: 4rem;
}

.game-banner-title {
  padding: 0px;
  margin: 0px;
  text-align: center;
}

.banner-icon-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.game-screen-icons {
  width: 2rem;
  padding: 0rem 2rem;
  cursor: pointer;
}

.game-screen-body {
  display: flex;
  flex-direction: row;
}

.game-screen-game {
  width: 48rem;
}

.winner-player-desc {
  font-size: 5rem;
  margin: 2rem 0rem;
  color: black;
  justify-content: center;
}

.winner-player-text {
  padding: 0rem 2rem;
  color: orange;
}

.score-board-div {
  border-color: black;
  border-style: solid;
  border-width: 0.2rem;
  width: 50%;
  margin: auto;
  margin-bottom: 2rem;
}

.game-over-text {
  font-size: 4rem;
  padding: 0rem 1rem;
  color: orange;
}

.game-over-scoreboard {
  font-size: 3rem;
  width: 35rem;
  padding: 0rem 1rem;
  color: orange;
}

.hide {
  display: none;
}

.default-line {
  background-color: grey;
}

.default-1-line {
  background-color: red;
}

.default-2-line {
  background-color: blue;
}

.default-3-line {
  background-color: yellow;
}

.box-default {
  background-color: beige;
}

.box {
  height: 3rem;
  width: 3rem;
}

.player-score-text {
  padding: 0rem 2rem;
  font-size: 2rem;
}

.player-score-color {
  width: 3rem;
  height: 1rem;
  margin: auto;
}

.player-score-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.border-bottom {
  border-bottom: 1px solid;
}

.bold-text {
  font-weight: bold;
}
